.write {
  padding-top: 50px;
}

.writeImg{
  margin-left: 150px;
  width:70vw;
  height:590px;
  border-radius: 10px;
  object-fit: cover;
}

.writeForm{
  position: relative;
}

.writeFormGroup {
  margin-left: 150px;
  display: flex;
  align-items: center;
}

.writeIcon {
  width: 20px;
  height: 20px;
  font-size: 20px;
  border: 1px solid;
  border-radius: 50%;
  color: rgb(129, 125, 125);
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.writeInput{
  font-size: 30px;
  border:none;
  padding:20px;
  width:70vw;
}

.writeInput:focus{
  outline: none;
}

.writeText{
  font-size: 20px;
  height: 100vh;
}

.writeSubmit{
  position:absolute;
  top:20px;
  right:50px;
  color:white;
  background-color: teal;
  padding:10px;
  border:none;
  border-radius: 10px;
  font-size: 16px;
  cursor: pointer;
}


